* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
}
body {
  background-color: rgb(232, 232, 232);
}

.list {
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 20px auto;
  background-color: #fff;
}
/* nth-child(2n) =>2배수
nth-child(odd) => 홀수
nth-child(even) => 짝수 */